import loginOther from '@/components/login/login-other.vue'
	export default {
		data() {
			return {
				//用户输入的内容
				userName:'',
				userPwd:'',
				//验证的规则
				rules:{
					userName:{
						rule:/\S/,
						msg:"账号不能为空",
					},
					userPwd:{
						rule:/^[0-9a-zA-Z]{6,16}$/,
						msg:"密码应该为6-16位字符"
					}
				}
			}
		},
		components:{
			loginOther
		},
		methods: {
			//关闭当前页面，返回上一页
			goBack(){
				uni.navigateBack({
					delta:1
				})
			},
			//点击登录
			submit(){
				if( !this.validate(userName)) return
				if( !this.validate(userPwd)) return
			},
			//判断验证是否符合要求
			validate(key){
				let bool = true
				if(!this.rules[key].rule.test(this[key])){
					uni.showToast({
						title:this.rules[key].msg,
						icon:'none'
					})
					bool = false
				}
				return bool
			}
		}
	}
